"use strict";
var $ = require('jquery');
var promise = require('es6-promise').Promise;
$(document).ready(function() {
    var retrieveSearch = function(searchTerm) {
        return new promise(function(resolve, reject) {
            $.ajax({
                url: "https://de.wikipedia.org/w/api.php",
                jsonp: "callback",
                dataType: 'jsonp',
                data: {
                    action: "query",
                    titles: searchTerm,
                    format: "json",
                    redirects: "true",
                    prop: 'extracts|revisions',
                    explaintext: 1,
                    exsentences: 1
                }
            }).done(function(data) {
                console.log(data.query.pages);
                var page = data.query.pages[Object.keys(data.query.pages)[0]];

                if ('missing' in page) {
                    reject("not found");
                } else {
                    $("#name").text(page.title);
                    $("#zuletzt").text(page.revisions[0].timestamp);
                    resolve({
                        title: page.title.replace(/\s/g, '_'),
                        pageid: page.pageid
                    });
                }
            }).fail(function(error) {
                reject(error);
            });
        });
    };

    var parseArticle = function(pageInfo) {
        $.ajax({
            url: "https://de.wikipedia.org/w/api.php",
            jsonp: "callback",
            dataType: 'jsonp',
            data: {
                action: "parse",
                format: "json",
                page: pageInfo.title,
                pageid: pageInfo.pageid,
                prop: "text|headitems|headhtml",
                mobileformat: "true",
                redirects: "true",
                disablelimitreport: "true",
                disableeditsection: "true"
            }
        }).done(function(data) {
            console.log(data);
            $("#content").html(data.parse.text[Object.keys(data.parse.text)[0]]);
        }).fail(function(error) {
            console.log(error);
        });
    };

    $("#button").click(function() {
        retrieveSearch($("#search").val()).then(
            parseArticle,
            function(error) {
                console.log(error);
            }
        );
    });
});